html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  src: local("Manrope-Regular"), local("Manrope-Regular"), url("../fonts/manrope-regular.woff2") format("woff2"), url("../fonts/manrope-regular.woff") format("woff");
}

@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 700;
  src: local("Manrope-Bold"), local("Manrope-Bold"), url("../fonts/manrope-bold.woff2") format("woff2"), url("../fonts/manrope-bold.woff") format("woff");
}

@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 300;
  src: local("Manrope-Light"), local("Manrope-Light"), url("../fonts/manrope-light.woff2") format("woff2"), url("../fonts/manrope-light.woff") format("woff");
}

@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 500;
  src: local("Manrope-Medium"), local("Manrope-Medium"), url("../fonts/manrope-medium.woff2") format("woff2"), url("../fonts/manrope-medium.woff") format("woff");
}

.icon-arrow-long {
  width: 14.49em;
  height: 1em;
  fill: currentColor;
}

.icon-arrow-right {
  width: 2.92em;
  height: 1em;
  fill: currentColor;
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: currentColor;
}

.icon-phone {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  line-height: 1.4;
  font-size: 16px;
  font-weight: 400;
  color: #333333;
  font-family: 'Manrope', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
}

a {
  text-decoration: none;
  color: #333333;
  transition: color .3s;
}

.no-touch a:hover {
  color: #0d9838;
}

.title {
  font-size: 30px;
  font-weight: 300;
  margin-bottom: 26px;
  text-align: center;
}

.title-xl {
  font-size: 60px;
  letter-spacing: .2px;
  font-weight: 300;
}

.title-lg {
  font-size: 36px;
  font-weight: 300;
  line-height: 1.4;
}

.title-sm {
  font-size: 24px;
  font-weight: 300;
  line-height: 1.5;
}

.btn {
  padding: 0 40px;
  height: 60px;
  text-transform: uppercase;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  font-size: 14px;
  transition: background-color .3s ease, color .3s ease;
}

.btn_lg {
  height: 70px;
}

.btn_white {
  color: #fff;
}

.btn_outline {
  background-color: transparent;
  border: 1px solid #f6f6f6;
  letter-spacing: 1.21px;
}

.no-touch .btn_outline:hover {
  background-color: #fff;
  color: #333333;
}

.no-touch .btn_outline:hover .btn__icon {
  transform: translateX(5px);
  color: #333333;
}

.btn_w100 {
  width: 100%;
}

.btn_green {
  background-color: #0d9838;
}

.btn_transf {
  transition: transform .3s ease;
}

.no-touch .btn_transf:hover {
  transform: translateY(2px);
}

.btn__icon {
  font-size: 7px;
  margin-left: 20px;
  transition: color .3s ease, transform .3s ease;
}

.btn__icon svg {
  display: block;
  max-width: 100%;
}

.btn__icon_green {
  color: #0d9838;
}

.divider {
  width: 37px;
  height: 1px;
  background-color: #0d9838;
  margin: 25px 0;
}

.divider-lg {
  width: 56px;
  height: 2px;
  background-color: #0d9838;
  margin: 40px 0;
}

.checkbox {
  position: relative;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}

.checkbox__input {
  display: none;
}

.checkbox__icon {
  position: relative;
  min-width: 30px;
  width: 30px;
  height: 30px;
  border-radius: 0;
  border: none;
  background-color: #f5f5f5;
  margin-right: 18px;
}

.no-touch .checkbox__icon:hover:before {
  transform: scale(0.5) rotate(45deg) translate(-1px, -1px);
}

.checkbox__icon:before {
  content: '';
  position: absolute;
  transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transform: scale(0) rotate(45deg) translate(-1px, -1px);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 1;
  border-right: 1px solid #0d9838;
  border-bottom: 1px solid #0d9838;
  height: 10px;
  width: 8px;
}

.checkbox__input:checked .checkbox__icon:before {
  transform: scale(1) rotate(45deg) translate(-1px, -1px);
}

.checkbox__input:checked + .checkbox__icon:before {
  transform: scale(1) rotate(45deg) translate(-1px, -1px);
}

.inner {
  max-width: 1270px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px 0 115px;
}

.block {
  display: flex;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100px;
  height: 100%;
  z-index: 100;
  min-height: 430px;
}

.header.is-active {
  width: 100%;
  height: 100px;
}

.header__wrap {
  position: absolute;
  top: 100px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(26, 29, 36, 0.6);
  transition: background-color .3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header.is-active .header__wrap {
  top: 0;
  background-color: #1a1d24;
}

.header__contacts {
  transform-origin: center;
  transform: rotate(-90deg);
  display: flex;
  align-items: center;
  justify-content: center;
}

.header.is-active .header__contacts {
  transform: translateY(-50%);
  min-height: 0;
}

.header__contacts a {
  display: block;
  flex-shrink: 0;
}

.header__contacts a:not(:first-child) {
  margin-left: 30px;
}

.phone,
.email {
  font-size: 22px;
  color: #fff;
}

.burger {
  position: relative;
  width: 100%;
  height: 100px;
  background-color: rgba(9, 10, 15, 0.7);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color .5s ease;
  z-index: 1;
}

.no-touch .burger:hover .burger__line {
  width: 100%;
}

.header.is-active .burger {
  width: 100px;
  height: 100%;
}

.burger__icon {
  display: block;
  width: 27px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.burger__line {
  display: block;
  width: 100%;
  height: 2px;
  margin: 6px 0;
  background-color: #fff;
  transform-origin: center;
  transition: width .3s, transform .3s, margin .3s;
}

.burger.is-active .burger__line {
  width: 100%;
  margin: 0;
  height: 0;
}

.burger__line_md {
  width: 73%;
  margin: 0;
}

.burger.is-active .burger__line_md {
  width: 100%;
  transform: rotate(45deg);
  transition-delay: .3s;
  height: 2px;
}

.burger__line_sm {
  width: 42%;
  margin: 0;
}

.burger.is-active .burger__line_sm {
  width: 100%;
  transform: rotate(-45deg);
  transition-delay: .3s;
  height: 2px;
  margin-top: -2px;
}

.burger-menu {
  position: fixed;
  left: 100px;
  top: 0;
  bottom: 0;
  width: 320px;
  background-color: #1a1d24;
  border-left: 1px solid #2d2d2d;
  transition: transform .3s ease, opacity .3s ease;
  transform: translateX(-100%);
  opacity: 0;
}

.burger-menu.is-open {
  transform: translateX(0);
  opacity: 1;
}

.burger-menu__inner {
  position: relative;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.burger-menu__item {
  position: relative;
  padding-left: 40px;
}

.burger-menu__item:not(:first-child) {
  margin-top: 20px;
}

.burger-menu__item.is-active:before {
  content: '';
  position: absolute;
  width: 20px;
  height: 2px;
  background-color: #0d9838;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.burger-menu__link {
  color: #fff;
  font-size: 18px;
  transition: color .3s;
}

.no-touch .burger-menu__link:hover {
  color: #0d9838;
}

.hero {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
}

.hero__content {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 100%;
  color: #fff;
}

.hero__contacts {
  position: absolute;
  left: 0;
  top: 0;
}

.hero__link {
  display: flex;
  align-items: center;
  height: 100px;
  color: #fff;
  font-size: 20px;
}

.hero__link-icon {
  font-size: 25px;
}

.hero__link-icon svg {
  display: block;
  max-width: 100%;
}

.hero__link-text {
  margin-left: 30px;
}

.hero__left {
  flex: 0 0 58.33333%;
  max-width: 58.33333%;
}

.hero__right {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
  margin-left: 20px;
  height: 100%;
}

.hero__subtitle {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.2);
  text-transform: uppercase;
  letter-spacing: 2.59px;
}

.hero__title:not(:first-child) {
  margin-top: 30px;
}

.hero__text {
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  margin: 40px 0 64px;
}

.hero-menu {
  height: 100%;
}

.hero-menu__item {
  position: relative;
  height: 33.33333%;
  display: flex;
  align-items: center;
  transition: height .3s ease, background-color .3s ease, border-color .3s ease;
  border: 1px solid #2d2d2d;
  color: #fff;
}

.no-touch .hero-menu__item:hover {
  color: #fff;
  background-color: rgba(26, 29, 36, 0.7);
  border-color: rgba(26, 29, 36, 0.7);
}

.no-touch .hero-menu__item:hover .hero-menu__continue {
  max-height: 80px;
}

.no-touch .hero-menu__item:hover .hero-menu__more {
  opacity: 1;
  transform: translateX(0);
}

.hero-menu__item:first-child, .hero-menu__item:last-child {
  border-top: none;
  border-bottom: none;
}

.hero-menu__wrap {
  padding: 20px 40px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.hero-menu__name, .hero-menu__continue, .hero-menu__more {
  display: block;
}

.hero-menu__continue {
  height: 80px;
  max-height: 0;
  position: relative;
  overflow: hidden;
  transition: max-height .3s ease;
}

.hero-menu__more {
  position: absolute;
  left: 0;
  bottom: 0;
  opacity: 0;
  transform: translateX(-40px);
  transition: transform .3s ease, opacity .3s ease;
  transition-delay: .2s;
  color: rgba(255, 255, 255, 0.3);
}

.arrow-more {
  display: flex;
  font-size: 12px;
  color: #333333;
}

.no-touch .arrow-more:hover {
  color: currentColor;
}

.no-touch .arrow-more:hover .arrow-more__icon-inner {
  max-width: 70px;
}

.arrow-more__text {
  transition: color .5s ease;
}

.arrow-more__icon {
  position: relative;
  max-width: 84px;
  width: 84px;
  margin-left: 20px;
  color: #4d4d4d;
  transition: color .5s ease;
}

.arrow-more__icon .icon {
  display: block;
}

.arrow-more__icon-inner {
  position: absolute;
  right: 0;
  max-width: 84px;
  width: 100%;
  overflow: hidden;
  height: 100%;
  transition: max-width .5s ease;
}

.arrow-more__icon-wrap {
  max-width: 84px;
  width: 84px;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
}

.about {
  padding: 100px 0;
}

.about__wrap {
  background-color: #f5f5f5;
  padding: 78px 85px 0 67px;
}

.about__content {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.about__left {
  flex: 0 0 41.66667%;
  max-width: 41.66667%;
  width: 100%;
  padding-right: 15px;
  align-self: flex-end;
}

.about__right {
  flex: 0 0 58.33333%;
  max-width: 58.33333%;
  width: 100%;
  padding-left: 85px;
  padding-bottom: 60px;
}

.about__img img {
  display: block;
  max-width: 100%;
  width: 100%;
}

.about__text {
  text-align: justify;
  margin-bottom: 40px;
}

.about__text p {
  line-height: 1.85;
}

.about__text p:not(:last-child) {
  margin-bottom: 27px;
}

.cards {
  padding-bottom: 100px;
}

.cards__content {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.cards__col {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}

.cards__col-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
  height: 100%;
}

.cards__item-wrap {
  padding-right: 15px;
  padding-left: 15px;
  width: 100%;
  flex: 0 0 100%;
  max-width: 100%;
  height: calc(50% - 12px);
}

.cards__item-wrap:not(:first-child) {
  margin-top: 24px;
}

.cards-item {
  padding: 65px;
  display: block;
  background-color: #fafafa;
  height: 100%;
}

.cards-item_image {
  color: #fff;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  z-index: 1;
  padding: 65px 55px;
}

.cards-item__header {
  margin-bottom: 20px;
}

.cards-item__header_mb {
  margin-bottom: 67px;
}

.cards-item__li {
  position: relative;
  padding-left: 36px;
}

.cards-item__li:before {
  content: '';
  position: absolute;
  width: 10px;
  height: 1px;
  left: 0;
  top: 11px;
  background-color: #0d9838;
}

.cards-item__li:not(:first-child) {
  margin-top: 20px;
}

.cards-item__link {
  border-bottom: 1px solid transparent;
  transition: border-color .3s, color .3s;
  letter-spacing: 0.38px;
  font-size: 15px;
}

.no-touch .cards-item__link:hover {
  border-color: currentColor;
}

.cards-item__phone {
  margin-bottom: 1rem;
}

.cards-item__phone a {
  color: #0d9838;
  font-size: 24px;
  font-weight: 300;
}

.no-touch .cards-item__phone a:hover {
  color: #096927;
}

.cards-item__text {
  font-size: 14px;
  line-height: 1.7;
  margin-bottom: 1.5rem;
}

.cards-item address {
  font-style: normal;
  font-size: 14px;
  line-height: 1.7;
}

.article-cart__text {
  margin: 10px 0 30px;
  font-size: 15px;
  line-height: 1.6;
  max-height: 70px;
  overflow: hidden;
}

.article-cart__more {
  opacity: 0.8;
}

.section-articles__title {
  margin-bottom: 50px;
}

.section-articles__wrap {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.section-articles__col {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-bottom: 90px;
}

.article-card {
  display: block;
}

.no-touch .article-card:hover {
  color: #333333;
}

.no-touch .article-card:hover .article-card__title {
  color: #0d9838;
}

.no-touch .article-card:hover .arrow-more__icon-inner {
  max-width: 70px;
}

.article-card__img {
  position: relative;
  display: block;
  height: 208px;
  overflow: hidden;
}

.no-touch .article-card__img:hover img {
  transform: scale(1);
}

.article-card__img:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #0d9838;
  opacity: 0.3;
  z-index: 1;
}

.article-card__img img {
  display: block;
  width: 100%;
  transform: scale(1.03);
  transition: transform .5s ease-in-out;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.article-card__date {
  display: block;
  font-size: 12px;
  color: rgba(51, 51, 51, 0.4);
  letter-spacing: 0.63px;
  text-transform: uppercase;
  margin-top: 30px;
}

.article-card__title {
  display: block;
  margin-top: 12px;
  transition: color .3s;
}

.article-card__text {
  display: block;
  margin-top: 20px;
  font-size: 14px;
  line-height: 1.7;
  height: 45px;
  overflow: hidden;
}

.article-card__more {
  display: block;
  margin-top: 30px;
  opacity: 0.8;
  align-self: end;
}

.section-feedback {
  padding-bottom: 120px;
  background-color: #ebebeb;
  overflow: hidden;
}

.section-feedback__wrap {
  display: flex;
  flex-wrap: wrap;
}

.section-feedback__left {
  padding-top: 120px;
  flex: 0 0 58.33333%;
  max-width: 58.33333%;
}

.section-feedback__right {
  position: relative;
  padding-top: 120px;
  padding-left: 90px;
  flex: 0 0 41.66667%;
  max-width: 41.66667%;
  z-index: 1;
}

.section-feedback__right:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  bottom: -120px;
  width: 50vw;
  background-color: #fff;
}

.section-feedback__title {
  margin-bottom: 32px;
}

.section-feedback__title span {
  color: #0d9838;
}

.feedback-card {
  height: 366px;
  padding: 70px 140px 70px 70px;
  background-color: #29292a;
  color: #fff;
  cursor: pointer;
}

.no-touch .feedback-card:hover .arrow-more__text,
.no-touch .feedback-card:hover .arrow-more__icon {
  color: rgba(255, 255, 255, 0.5);
}

.no-touch .feedback-card:hover .arrow-more__icon-inner {
  max-width: 70px;
}

.feedback-card:not(:first-child) {
  margin-top: 30px;
}

.feedback-card__name {
  font-size: 18px;
  font-weight: 300;
}

.feedback-card__text {
  margin-top: 21px;
  height: 100px;
  overflow: hidden;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  line-height: 1.72;
  letter-spacing: 0.3px;
}

.feedback-card__more {
  margin-top: 47px;
}

.feedback-card__more .arrow-more__text,
.feedback-card__more .arrow-more__icon {
  color: rgba(255, 255, 255, 0.3);
}

.form-group {
  margin-bottom: 24px;
}

.form-input {
  display: block;
  width: 100%;
  padding: .375rem 20px;
  font-size: 14px;
  line-height: 1.5;
  height: 70px;
  border-radius: 0;
  font-size: 14px;
  color: #333333;
  background-color: #f5f5f5;
  border: none;
  font-family: inherit;
}

.form-input:focus::placeholder {
  font-size: 0;
}

textarea.form-input {
  height: auto;
  padding: 20px;
  resize: none;
  min-height: 118px;
}

[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(54, 54, 54, 0.7);
  user-select: none;
  text-align: center;
  font-size: 0;
  transform: translate3d(0, 0, 0);
  overflow: auto;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
}

[data-modal]:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
  z-index: 100;
}

[data-modal-container] {
  position: relative;
  z-index: 1;
  font-size: 12px;
  margin: 10px;
  vertical-align: middle;
  display: inline-block;
  max-width: 650px;
  width: 100%;
  padding: 60px;
  background-color: #fff;
}

[data-modal-close] {
  position: absolute;
  top: 20px;
  right: 40px;
  background-color: transparent;
  font-size: 0;
}

[data-modal-close] .icon {
  font-size: 17px;
}

.feedback-modal {
  text-align: left;
  font-size: 16px;
  line-height: 1.625;
}

.feedback-modal__name {
  font-size: 30px;
  font-weight: 300;
  margin-bottom: 21px;
}

.feedback-modal__text {
  font-size: 16px;
  letter-spacing: 0.3px;
}

.feedback-modal__text p {
  margin: 0;
}

.feedback-modal__text p:not(:first-child) {
  margin-top: 23px;
}

.footer {
  padding: 60px 0;
}

.footer__wrap {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.footer__left, .footer__right, .footer__center, .footer__col, .footer__item {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}

.footer__left {
  flex: 0 0 25%;
  max-width: 25%;
}

.footer__center {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
}

.footer__right {
  flex: 0 0 41.66667%;
  max-width: 41.66667%;
}

.footer__col {
  flex: 0 0 41.66667%;
  max-width: 41.66667%;
}

.footer__col_xl {
  flex: 0 0 58.33333%;
  max-width: 58.33333%;
}

.footer__founder {
  font-size: 24px;
  line-height: 1.5;
  margin-bottom: 1rem;
}

.footer__email {
  font-size: 14px;
  opacity: 0.4;
  transition: opacity .5s ease, color .5s ease;
}

.no-touch .footer__email:hover {
  opacity: 1;
  color: #0d9838;
}

.footer__item {
  flex: 0 0 50%;
  max-width: 50%;
}

.footer__tel {
  white-space: nowrap;
  font-size: 20px;
}

.no-touch .footer__tel:hover {
  color: #333333;
}

.footer__worktime {
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 1.12px;
}

.footer-menu__header {
  margin-bottom: 13px;
  font-size: 14px;
  color: #0d9838;
  line-height: 1.85;
  letter-spacing: 0.75px;
  text-transform: uppercase;
}

.footer-menu__item:not(:first-child) {
  margin-top: 20px;
}
