//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$manrope: 'Manrope';
@mixin manrope {
	font-family: 'Manrope', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif; }

@font-face {
  @include fontdefault(normal, 400, $manrope);
  @include fontpath('Manrope-Regular', 'Manrope-Regular', manrope-regular); }

@font-face {
  @include fontdefault(normal, 700, $manrope);
  @include fontpath('Manrope-Bold', 'Manrope-Bold', manrope-bold); }

@font-face {
  @include fontdefault(normal, 300, $manrope);
  @include fontpath('Manrope-Light', 'Manrope-Light', manrope-light); }

@font-face {
  @include fontdefault(normal, 500, $manrope);
  @include fontpath('Manrope-Medium', 'Manrope-Medium', manrope-medium); }
